import React, { Component } from "react";
import "../App.css";
import { Switch, Route } from "react-router-dom";
import Header from "./Header";
import Main from "./Main";
import Person from "./Person";
import NoMatch from "./NoMatch";
import Footer from "./Footer";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = { value: "Henry the III of England" };
    this.changeHandlerText = this.changeHandlerText.bind(this);
    this.clickHandler = this.clickHandler.bind(this);
  }
  changeHandlerText(event) {
    this.setState({ value: event.target.value });
  }
  clickHandler(event) {
      alert('hej')
  }
  render() {
    return (
      <>
        <Header name={this.state.value} />
        <Switch>
          <Route exact path="/" 
          render={props => <Main {...props} value={this.state.value} changeHandlerText={this.changeHandlerText} />}
          />
          <Route exact path="/:text" component={Main} />
          <Route path="/Person/:text" component={Person} />
          <Route component={NoMatch} />
        </Switch>
        <Footer />
      </>
    );
  }
}

export default App;
