import React, { Component } from "react";
import { Link } from "react-router-dom";
import henryiii from "../img/Henryiii.jpg";

class Person extends Component {
  render() {
    return (
      <div className="Page">
        {/* <h2>Henry the III of England</h2> */}
        <h2>{this.props.match.params.text}</h2>
        <a
          href="https://www.totallytimelines.com/henry-iii-1207-1272/"
          target="_blank"
          rel="noopener noreferrer"
        >
          <img
            src={henryiii}
            alt="henry the 3rd of England"
            className="App-logo"
          />
        </a>
        <Link to="/">Back</Link>
        {/* <p>
          En sida med bild, namn och text som beskriver en person. Den routas
          till “/person” och har en Link som går till första sidan.
        </p> */}
        <p>
          Henry III (1 October 1207 – 16 November 1272), also known as Henry of
          Winchester, was King of England, Lord of Ireland, and Duke of
          Aquitaine from 1216 until his death. The son of King John and Isabella
          of Angoulême, Henry assumed the throne when he was only nine in the
          middle of the First Barons' War. Cardinal Guala declared the war
          against the rebel barons to be a religious crusade and Henry's forces,
          led by William Marshal, defeated the rebels at the battles of Lincoln
          and Sandwich in 1217. Henry promised to abide by the Great Charter of
          1225, which limited royal power and protected the rights of the major
          barons. His early rule was dominated first by Hubert de Burgh and then
          Peter des Roches, who re-established royal authority after the war. In
          1230, the King attempted to reconquer the provinces of France that had
          once belonged to his father, but the invasion was a debacle. A revolt
          led by William Marshal's son, Richard, broke out in 1232, ending in a
          peace settlement negotiated by the Church.
        </p>
      </div>
    );
  }
}

export default Person;
